import posts from '../posts.json'

export default () => (
	<div>
		<h1>Blog</h1>
		<ul>
			{
				Object.entries(posts).map((value, index) => {
					return (
						<li key={index}>
							{value}
						</li>
					)
				})
			}
		</ul>
	</div>
)
